import axios from 'axios'

//const CRIPTOS_API_BASE_URL = 'http://localhost:8080/criptos'
const CRIPTOS_API_BASE_URL = 'https://crypto-caff.herokuapp.com/criptos'
                             
class ServicoCriptos{
    getCriptos(){
        return axios.get(CRIPTOS_API_BASE_URL);
    }
    getCriptosDetalhado(id){
        return axios.get(CRIPTOS_API_BASE_URL+"/"+id);
    }
    setCriptos(data){
        return axios.post(CRIPTOS_API_BASE_URL, data)
    }
    deleteCriptos(id){
        return axios.delete(CRIPTOS_API_BASE_URL+"/"+id);
    }
    atualizarCriptos(id, data){
        return axios.put(CRIPTOS_API_BASE_URL+"/"+id, data)
    }
}
export default new ServicoCriptos()