import axios from 'axios'

//const COMPRAS_API_BASE_URL = 'http://localhost:8080/compras'
const COMPRAS_API_BASE_URL = 'https://crypto-caff.herokuapp.com/compras'

class ServicoCompras{
    getCompras(){
        return axios.get(COMPRAS_API_BASE_URL);
    }
    getComprasDetalhado(id){
        return axios.get(COMPRAS_API_BASE_URL+"/"+id);
    }
    setCompras(data){
        return axios.post(COMPRAS_API_BASE_URL, data)
    }
    getTotalProdutos(){
        const compras2 = axios.get(COMPRAS_API_BASE_URL);
        const compras = compras2.reduce(getTotal,0);
        function getTotal(compras, item) {
            return compras + (item.quantidade);
        }
        return compras;
    }
    deleteCompras(id){
        return axios.delete(COMPRAS_API_BASE_URL+"/"+id);
    }
    atualizarCompras(id, data){
        return axios.put(COMPRAS_API_BASE_URL+"/"+id, data)
    }
}
export default new ServicoCompras()