        <b-card-text>Quantidade: <span class="label">{{ parseFloat(pms.qtd).toFixed(7) }}</span></b-card-text>
        <b-card-text>Investimento: <span class="label">${{ (parseFloat(pms.val).toFixed(2)) }}</span></b-card-text>
        <b-button variant="info" class="ml-3" @click='showTable()'>Análise Técnica</b-button>
        <b-button variant="info" class="ml-3" @click='showGrafico()'>Gráfico Avançado</b-button>
      </b-card>       
  </div>
