<script>
import ServicoCompras from '@/servicos/ServicoCompras'
export default {
  name: "Listcompras",
  data() {
    return {
      compras: [],
      custoTotal: 0,
      totalProdutos: 0,
      totalInvestido: 0,
      pms: {
