      },
      vetpms: [],
      vetmoedas: []
    };
  },
  created() {
       //if(this.$route.params.index === 0 || this.$route.params.index !== undefined){
        ServicoCompras.getComprasDetalhado(this.$route.params.index).then((response) => {
        this.vetpms = response.data;
        this.pms.descricao = this.vetpms.descricao;
        this.calculos(this.pms.descricao);
      })      
  },
  mounted(){
    this.removerTabela();
  },
  methods: {
    showTable(){
          let x = this.pms.descricao;
          let Script = document.createElement('script')
         // recaptchaScript.setAttribute('id', 'tabela')
          Script.setAttribute('src', "https://s3.tradingview.com/external-embedding/embed-widget-technical-analysis.js");
          Script.setAttribute('async', 'async');
          Script.text = `
          {
          "interval": "1D",
          "width": 425,
          "isTransparent": false,
          "height": 450,
          "symbol": "BINANCE:${x}USDT",
          "showIntervalTabs": true,
          "locale": "br",
          "colorTheme": "dark"
          } `
          
          document.body.appendChild(Script)

      },
      showGrafico(){
          
          let x = this.pms.descricao;
          
          let Script2 = document.createElement('script')
         // recaptchaScript.setAttribute('id', 'tabela')
          Script2.setAttribute('src', "https://s3.tradingview.com/tv.js")
          //Script2.setAttribute('async', 'async');
