            }
          );
          `
          document.body.appendChild(Script2)
      },
    removerTabela(){
        var el = document.querySelector('iframe');
        console.log(el);
        if(el)
          el.remove();
      },
    calculos(moeda){
      this.getTotalProdutosByMoeda(moeda);
      this.getTotalInvestidoByMoeda(moeda);
      this.getPrecoMedioByMoeda();
    },
    getCompras(){
      ServicoCompras.getCompras().then((response) => {
        this.compras = response.data;
      })
    },
    getTotalProdutosByMoeda(moeda){
        ServicoCompras.getCompras().then((response) => {
        const obj = response.data.reduce(getTotal,0);
        function getTotal(obj, item) {
          return (item.descricao===moeda && (obj + (item.quantidade)) || obj);
         }
      
        this.pms.qtd=obj;
      })
    },
    
    getTotalInvestidoByMoeda(moeda){
        ServicoCompras.getCompras().then((response) => {
        const obj = response.data.reduce(getTotal,0);
        function getTotal(obj, item) {
          return (item.descricao===moeda && (obj + (item.preco_total)) || obj);
        }
        this.pms.val=obj;
        //return obj;
      })
    },
    getPrecoMedioByMoeda(){
      ServicoCompras.getCompras().then((response) => {
        const obj = response.data.reduce(getTotal,0);
        function getTotal() {
            return ;
        }
        this.pms.pm=this.pms.val/this.pms.qtd;
        console.log(obj);
      })
    },
    getMoedasCompradas(){
        ServicoCompras.getCompras().then((response) => {    
        const moedas = response.data;
        var m = [];
        for (var i in moedas){
             m.push(moedas[i].descricao);
        }
        //console.log(m);
        this.vetmoedas=m;
        return m;
      })
    },
  },
};
</script>

