        <!-- <b-card-text>Id: {{ compra.id }}</b-card-text> -->
        <b-card-text>Preço: <span class="label">${{ parseFloat(compra.preco).toFixed(7) }}</span></b-card-text>
        <b-card-text>Quantidade: {{ parseFloat(compra.quantidade).toFixed(7) }}</b-card-text>
        <!-- <b-card-text>Preço Unitário: R${{ parseFloat(produto.precoUnitario).toFixed(2) }}</b-card-text> -->
        <!-- <b-card-text>Preço Mínimo de Venda: R${{ parseFloat(produto.precoMin).toFixed(2) }}</b-card-text> -->
        <b-card-text>Total: <span class="label">${{ (parseFloat(compra.preco_total)).toFixed(2) }}</span></b-card-text>

