<script>
import ServicoCompras from '@/servicos/ServicoCompras'
import ServicoCriptos from '@/servicos/ServicoCriptos'
export default {
  name: "Listcompras",
  data() {
    return {
      compras: [],
      compraSelecionada: [],
      custoTotal: 0,
      totalProdutos: 0,
      totalInvestido: 0,
      pms: {
        descricao: "",
        qtd: 0,
        val: 0,
        pm: 0,
      },
      vetpms: [],
      busca: null,
      criptos: [],
    };
  },
  mounted(){
    this.removerTabela();
     this.getCompras();
    //this.getTotalProdutos();
    this.getTotalInvestido();  
  },
  created() {
    this.getAtivos();
    this.getCompras();  
  },

  methods: {
    removerTabela(){
        var el = document.querySelector('iframe');
        console.log(el);
        if(el)
          el.remove();
      },
    info(index) {
      this.$router.push({ name: "infos", params: { index } });
    },
    edit(compra) {
      var index = compra.id;
      console.log(compra);
      this.$router.push({ name: "compras", params: { compra, index } });
    },
    remove(compra, index) {
      this.compraSelecionada = compra;
      this.compraSelecionada.index = index;
      this.$refs.modalRemove.show();
    },
    hideModal() {
      this.$refs.modalRemove.hide();
    },
    confirmRemoveCompra(index) {
      ServicoCompras.deleteCompras(index).then((response) => {
        this.compras = response.data;
         this.getCompras();
         this.busca=null;
      })
      this.hideModal();
    },
    getCompras(){
      ServicoCompras.getCompras().then((response) => {
        this.compras = response.data;
      })
    },
    getTotalProdutos(){
        ServicoCompras.getCompras().then((response) => {
        
        const obj = response.data.reduce(getTotal,0);
        function getTotal(obj, item) {
            return obj + (item.quantidade);
        }
        this.totalProdutos = obj;
      })
    },
    getTotalInvestido(){
        ServicoCompras.getCompras().then((response) => {
        
        const obj = response.data.reduce(getTotal,0);
        
        function getTotal(obj, item) {
            return obj + (item.preco_total);
        }
        this.totalInvestido = obj;
      })
    },
    buscar(moeda){
         console.log(this.busca);
        ServicoCompras.getCompras().then((response) => {    
        const moedas = response.data.filter(buscarMoeda);
        
        function buscarMoeda(valor) {
           if(valor.descricao == moeda)
             return valor;    
        }
        //for (var i in moedas){
           //  this.compras.push(moedas[i].descricao);
        //}
        //console.log(moedas);
        this.compras = moedas;
      })
    },
    getAtivos(){
       /* this.criptos = [
          { value: 'ADA', text: 'ADA' },
          { value: 'ALICE', text: 'ALICE' },
          { value: 'AR', text: 'AR' },
          { value: 'AXS', text: 'AXS' },
          { value: 'BTC', text: 'BTC' },
          { value: 'CHR', text: 'CHR' },
          { value: 'CHZ', text: 'CHZ' },
          { value: 'CVC', text: 'CVC' },
          { value: 'DOT', text: 'DOT' },
          { value: 'ENJ', text: 'ENJ' },
          { value: 'ETH', text: 'ETH' },
          { value: 'FTM', text: 'FTM' },
          { value: 'LRC', text: 'LRC' },
          { value: 'MATIC', text: 'MATIC' },
          { value: 'ONE', text: 'ONE' },
          { value: 'SOL', text: 'SOL' },
          { value: 'SAND', text: 'SAND' },
          { value: 'SHIBA', text: 'SHIBA' },
          { value: 'XRP', text: 'XRP' }
       ] */
       //this.criptos= [];
       ServicoCriptos.getCriptos().then((response) => {
        const vet = response.data;   
        for (var i in vet){
             this.criptos.push({value: vet[i].descricao, text: vet[i].descricao});
        }
        this.criptos.sort(function(a,b) {
          if(a.value < b.value) return -1;
          if(a.value > b.value) return 1;
          return 0;
        });
       // console.log(this.criptos);
      })
     }
  },
};
</script>

