import Vue from 'vue'
import VueRouter from 'vue-router'
import ListCompras from '../views/ListCompras.vue'
import Compras from '../views/Compras.vue'
import Login from '../views/Login.vue'
import Home from '../views/Home.vue'
import Infos from '../views/Infos.vue'
//import moeda from '../views/Moeda.vue'

Vue.use(VueRouter)
//Vue.use(moeda)
const routes = [
  {
    path: '/listcompras',
    name: 'listcompras',
    component: ListCompras
  },
  {
    path: '/infos',
    name: 'infos',
    component: Infos
  },
  {
    path: '/compras',
    name: 'compras',
    component: Compras
  },
  {
    path: '/home',
    name: 'home',
    component: Home
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
