<script>
import ServicoCompras from '@/servicos/ServicoCompras'
import ServicoCriptos from '@/servicos/ServicoCriptos'
//import {db} from '@/firebase/db.js'
import ToastMixin from "@/mixins/toastMixin.js"
export default {
  name: "Produto",
  mixins:[ToastMixin],
  data() {
    return {
      form: {
        descricao: null,
        preco: null,
        quantidade: 0,
        preco_total: 0,
        
      },
      form_modal: {
        descricao: null,
        preco: null,
                
      },
      methodSave:"new",
      qtdProdutos: 0,
      criptos: [],
      modal: false
      
    }
  },

  created(){
    this.getAtivos();
    if(this.$route.params.index === 0 || this.$route.params.index !== undefined){
        this.methodSave = "update";
        this.form = this.$route.params.compra;
     }
  },
   mounted(){
  //  this.getAtivos();
  this.removerTabela();
  },

  methods: {
    removerTabela(){
        var el = document.querySelector('iframe');
        console.log(el);
        if(el)
          el.remove();
      },
    saveProduto() {
      if(this.methodSave === "update") {
         console.log(this.form);
          ServicoCompras.atualizarCompras(this.form.id, this.form)
          .then((response) => {
        
         console.log(response);
       })

        this.showToast("success", "Sucesso!", "Compra atualizada com sucesso!");
        this.$router.push({name: "listcompras"});
      
      }else{
        ServicoCompras.setCompras({descricao: this.form.descricao, preco: this.form.preco, quantidade: this.form.quantidade, preco_total: this.form.preco_total})
         .then((response) => {
        
        console.log(response);
         })
        this.showToast("success", "Sucesso!", "Compra realizada com sucesso!");
      
        this.$router.push({name: "listcompras"});
      }
    ServicoCompras.getCompras().then((response) => {
        this.compras = response.data;
      })
      //this.somarQtdProdutos();
      //this.calcularPrecoMin();
    },
    calcularPrecoTotal() {
       this.form.preco_total = parseFloat(this.form.preco * this.form.quantidade).toFixed(2);
     },  
     calcularQtd() {
       this.form.quantidade = parseFloat(this.form.preco_total / this.form.preco).toFixed(5);
     },
     saveCripto() {
       ServicoCriptos.setCriptos({descricao: this.form_modal.descricao, preco: this.form_modal.preco})
         .then((response) => {
            console.log(response);
         })
         this.criptos.push({value: this.form_modal.descricao, text: this.form_modal.descricao});
         this.form_modal.descricao=null;
         this.form_modal.preco=null;
         this.modal=false;
        
     //  this.showToast("success", "Sucesso!", "Cripto cadastrado com sucesso!");
                    
       //ServicoCriptos.getCritpos().then((response) => {
        //this.compras = response.data;
      //})
      
    },
    abrirModal() {
       this.modal = !this.modal;
       //this.getAtivos();
     },
     getAtivos(){
       /* this.criptos = [
          { value: 'ADA', text: 'ADA' },
          { value: 'ALICE', text: 'ALICE' },
          { value: 'AR', text: 'AR' },
          { value: 'AXS', text: 'AXS' },
          { value: 'BTC', text: 'BTC' },
          { value: 'CHR', text: 'CHR' },
          { value: 'CHZ', text: 'CHZ' },
          { value: 'CVC', text: 'CVC' },
          { value: 'DOT', text: 'DOT' },
          { value: 'ENJ', text: 'ENJ' },
          { value: 'ETH', text: 'ETH' },
          { value: 'FTM', text: 'FTM' },
          { value: 'LRC', text: 'LRC' },
          { value: 'MATIC', text: 'MATIC' },
          { value: 'ONE', text: 'ONE' },
          { value: 'SOL', text: 'SOL' },
          { value: 'SAND', text: 'SAND' },
          { value: 'SHIBA', text: 'SHIBA' },
          { value: 'XRP', text: 'XRP' }
       ] */
       //this.criptos= [];
       ServicoCriptos.getCriptos().then((response) => {
        const vet = response.data;
        //var m = [];
        for (var i in vet){
             this.criptos.push({value: vet[i].descricao, text: vet[i].descricao});
        }
        //this.criptos = this.criptos.sort();
        this.criptos.sort(function(a,b) {
          if(a.value < b.value) return -1;
          if(a.value > b.value) return 1;
          return 0;
        });
        //console.log(this.criptos);
      })
     }

  }

}
</script>

